import {useState} from 'react'
import { useSelector } from 'react-redux'
import Typography from '@mui/material/Typography';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import StepContent from '@mui/material/StepContent';
import Button from '@mui/material/Button';


const steps = [
    {
        label: 'Software',
        subtitle: 'Como o prottipo identifica um obstaculo?',
        description: `Foi desenvolvido um cdigo em C++ projetado para realizar um mapeamento da distncia entre o
        sensor e qualquer obstculo. A proximidade do obstculo  refletida na frequncia das pulsaes do motor de
        vibrao, que aumenta  medida que o obstculo se aproxima e diminui  medida que se afasta. O cdigo 
        configurado para ler os dados do sensor e, com base nessa leitura, calcular um tempo de atraso apropriado,
        a qual  diretamente relacionado  distncia do obstculo. Isso permite que o motor de vibrao seja acionado
        em uma frequncia que os usurios possam sentir e interpretar, dando resposta ttil sobre a proximidade de
        objetos.`,
    },
    {
        label: 'Hardware',
        subtitle: 'Como funciona a parte eltrica?',
        description: `A estrutura fsica do prottipo foi modelada e produzida em filamento de PLA branco, um
        termoplstico biodegradvel originado de recursos renovveis, como amido de milho ou cana-de-acar.
        A produo do modelo 3D durou aproximadamente 3 horas para cada um dos dois mdulos e permitiu a criao de uma
        estrutura robusta que facilitou a integrao do sensor, do motor de vibrao, do Arduino e da bateria.
        Vale ressaltar que os dois mdulos foram projetados com um mecanismo que impede o desencaixe acidental,
        garantindo estabilidade. Para isso, as duas peas foram configuradas de modo que fiquem conectadas efetuando o
        encaixe de maneira segura.`,
    },
    {
        label: 'Chassi',
        subtitle: 'Onde a parte eltrica fica?',
        description: `A estrutura fsica do prottipo foi modelada e produzida em filamento de PLA branco, um
        termoplstico biodegradvel originado de recursos renovveis, como amido de milho ou cana-de-acar.
        A produo do modelo 3D durou aproximadamente 3 horas para cada um dos dois mdulos e permitiu a criao de uma
        estrutura robusta que facilitou a integrao do sensor, do motor de vibrao, do Arduino e da bateria.
        Vale ressaltar que os dois mdulos foram projetados com um mecanismo que impede o desencaixe acidental,
        garantindo estabilidade. Para isso, as duas peas foram configuradas de modo que fiquem conectadas efetuando o
        encaixe de maneira segura.`,
    },
];


function Project(){
    const projectStyle = useSelector((state) => state.styles.style.sections.sectionContentStyle.project)

    const [activeStep, setActiveStep] = useState(0);

    const handleNext = () => {
        setActiveStep((prevActiveStep) => prevActiveStep + 1 !== steps.length ? prevActiveStep + 1 : 0);
    };

    const handleBack = () => {
        setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };

    return (
        <Stepper activeStep={activeStep} orientation="vertical">
            {steps.map((step, index) => (
                <Step key={step.label} >
                    <StepLabel
                        optional={
                            (
                                <Typography {...projectStyle.stepSubTitleTypography}>{step.subtitle}</Typography>
                            )
                        }
                    >
                        <Typography {...projectStyle.stepTitleTypography}>{step.label}</Typography>
                    </StepLabel>
                    <StepContent>
                        <Typography {...projectStyle.typography}>{step.description}</Typography>
                        <div>
                            <Button
                                variant="contained"
                                onClick={handleNext}
                            >
                                {index === steps.length - 1 ? 'Inicio' : 'Proximo'}
                            </Button>
                            <Button
                                disabled={index === 0}
                                onClick={handleBack}
                                sx={{ mt: 1, mr: 1 }}
                            >
                                Voltar
                            </Button>
                        </div>
                    </StepContent>
                </Step>
            ))}
        </Stepper>
    );
}


export default Project;