import { useSelector } from 'react-redux'
import Typography from '@mui/material/Typography';
import Link from '@mui/material/Link';

function Intro(){
    const introStyle = useSelector((state) => state.styles.style.sections.sectionContentStyle.intro)
    const censo_2019 = 'https://biblioteca.ibge.gov.br/visualizacao/livros/liv101846.pdf'
    const lei_brasileira_inclusao_pessoa_com_deficiencia = 'https://www.planalto.gov.br/ccivil_03/_ato2015-2018/2015/lei/l13146.htm'

    const goTo = (event) => {
        event.preventDefault()
        window.open(event.target.href, "_blank");
    }

    return(
        <>
            <Typography {...introStyle.typography}>
                A viso  um dos sentidos mais importantes para o ser humano, pois, a partir dela,  possvel se relacionar
                com o ambiente, registrando e organizando informaes,  nvel cerebral, sendo  longa ou curta distncia.
            </Typography>
            <Typography {...introStyle.typography}>
                Segundo pesquisa <Link href={censo_2019} underline="hover" onClick={goTo} >Ciclos de vida de 2019</Link>, realizada pelo Instituto Brasileiro de Geografia e
                Estatstica (IBGE), o Brasil possua cerca de <b>7 Milhes de habitantes</b> considerados deficientes visuais.
                Entende-se por Deficincia Visual um grande espectro de nveis de viso, podendo compreender desde a
                ausncia de viso perifrica at a ausncia total de viso.
            </Typography>
            <Typography {...introStyle.typography}>
                Segundo o <b>Artigo 46</b> da <Link href={lei_brasileira_inclusao_pessoa_com_deficiencia} underline="hover" onClick={goTo}>Lei Brasileira de Incluso da Pessoa com Deficincia</Link>,  assegurado o <b>direito ao
                transporte e  mobilidade</b> das pessoas com deficincia, em igualdade de oportunidades com as demais
                pessoas, por meio da identificao e eliminao de todos os obstculos e barreiras ao seu acesso.
                Entretanto, essa garantia <b>no reflete a realidade dessa populao</b>.
            </Typography>
        </>

    )
}

export default Intro;